* {
  margin: 0;
  padding: 0;
}

html {
  -webkit-user-select: none;
  user-select: none;
}

body {
  height: 100%;
  width: 100%;
  background-color: #faf8f0;
  font-family: Varela Round, sans-serif;
  font-size: 16px;
}

h1 {
  color: #776e65;
  font-size: 64px;
}

.container {
  width: 600px;
  flex-direction: column;
  align-items: center;
  margin: 20px auto;
  display: flex;
}

.title {
  justify-content: space-between;
  margin-bottom: 20px;
  display: flex;
}

.display-banner {
  float: right;
  display: flex;
}

.display-scores {
  text-align: center;
  width: 50px;
  height: 40px;
  text-transform: uppercase;
  color: #f5f5f5;
  background-color: #bbada0;
  border-radius: 3px;
  margin-left: 20px;
  padding: 20px;
  font-size: 16px;
  display: block;
  position: relative;
}

.intro {
  width: 100%;
  font-size: 20px;
  line-height: 1.5;
}

#new-btn {
  height: 30px;
  color: #f5f5f5;
  float: right;
  background-color: #bbada0;
  border-style: none;
  border-radius: 10px;
  outline: 0;
  padding: 4px;
}

#new-btn:hover {
  background-color: #afa184;
  border-radius: 10px;
}

#game-board {
  grid-template-columns: repeat(var(--grid-size), var(--cell-size));
  grid-template-rows: repeat(var(--grid-size), var(--cell-size));
  font-size: calc(var(--cell-size) / 8 * 3);
  gap: var(--cell-gap);
  padding: var(--cell-gap);
  background-color: #bbada0;
  border-radius: 1vmin;
  margin-top: 30px;
  display: grid;
  position: relative;
}

.cell {
  background-color: #afa184;
  border-radius: 1vmin;
}

.tile {
  width: var(--cell-size);
  height: var(--cell-size);
  top: calc(var(--y) * (var(--cell-size)  + var(--cell-gap))  + var(--cell-gap));
  left: calc(var(--x) * (var(--cell-size)  + var(--cell-gap))  + var(--cell-gap));
  color: #afa192;
  border-radius: 1vmin;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  transition: all .1s ease-in-out;
  animation: .2s ease-in-out show;
  display: flex;
  position: absolute;
}

[data-digits="0"] {
  color: #afa184 !important;
}

[data-digits="1"] {
  font-size: 60px;
}

[data-digits="2"] {
  font-size: 48px;
}

[data-digits="3"] {
  font-size: 36px;
}

[data-digits="4"] {
  font-size: 32px;
}

@keyframes show {
  0% {
    opacity: .5;
    transform: scale(0);
  }
}

